import React from 'react';
import './index.css';

class Nav extends React.Component {
    render() {
        return (
            <div className="nav-bar">
                <div className="nav-bar-left"></div>
                <div className="nav-bar-right"></div>
            </div>
        )
    }
}

export default Nav;


